import React from "react";

import MyModal from "./parts/Modal/Modal";
import Form from "components/Form/Form";
import Container from "./parts/ResultsPageContainer";
import ErrorMessage from "components/ErrorMessage";
import ImagesSection from "./parts/ImagesSection/ImagesSection";

import { withFormContainer } from "hocs";
import FakeHeader from "components/HiddenH1";
import { useClearOnMount } from "hooks/useClearOnMount";

const WrappedForm = withFormContainer(Form);

const ResultsPage = () => {
    useClearOnMount();
    return (
        <Container>
            <FakeHeader text="Matching images" />
            <ErrorMessage />
            <WrappedForm />
            <MyModal />
            <ImagesSection />
        </Container>
    );
};

export default ResultsPage;
